           <v-flex xs2 sm4 md2>
            <div class="caption grey--text">Due by</div>
            <div>{{project.due}}</div>
          </v-flex>
           <v-flex xs6 sm4 md2>
             <div class="right">
              <v-chip :color="project_status(`${project.status}`)" small class="white--text my-2 caption">{{project.status}}</v-chip>
             </div>
          </v-flex>


        </v-layout>
      </v-card>
    </v-container>


